import './Calculator.scss';
import { CustomSelect, ISelectItem } from '../Calculator/components/CustomSelect/CustomSelect';
import { Title } from '../../../../components/TitleofSection/Title';
// import airplaneImg from '../Calculator/images/calculator.png';
import { Calculate } from './Calculate';
import { CalculatorButton } from './components/CalculatorButton';
import { Controller, SubmitHandler, useForm } from 'react-hook-form';
import { Form } from 'react-router-dom';
import { ValuePrinter } from './components/ValuePrinter';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../../app/store';
import { changeResult } from '../../redux/homePageSlice';

const options = [{ value: 'Trkiy', label: 'Trkiy' }];
const units = [
  { value: 'kg', label: 'kg' },
  { value: 'g', label: 'g' },
];
export type CalculateFormData = {
  country: ISelectItem;
  length: number;
  width: number;
  height: number;
  unitWeight: ISelectItem;
  weight: number;
};

export const Calculator = () => {
  const calculatorState = useSelector((state: RootState) => state.homePage);
  const dispatch = useDispatch();
  const calculateHandler = () => dispatch(setCalculationResult());
  const {
    register,
    handleSubmit,
    control,
    formState: { errors },
  } = useForm<CalculateFormData>();

  const onSubmit: SubmitHandler<CalculateFormData> = (data) => {
    dispatch(
      changeResult(
        Calculate(
          data.length,
          data.width,
          data.height,
          data.weight,
          data.unitWeight.value,
          data.country.value,
        ),
      ),
    );
  };
  return (
    <section id="calculator">
      <div className="myContainer">
        <Title value="KALKULYATOR" isWhite />
        <div className="summary">
          <p>
            Hr hans bir uzunluq gstiricisi 60 sm v 60 sm-dn artq olan balamalarn danma haqq hm
            hcm kisi, hm d fiziki kisi hesablanacaq, hans ki gstricisi daha ox olarsa, danma
            haqq o ki sasnda hesablanacaqdr. En (sm) x Uzunluq (sm) x Hndrlk (sm)/6000 = Hcm kisi
            (kq)
          </p>
        </div>
        <div className="flex-row">
          <div className="form-container">
            <p>lk, ki mlumatlarn daxil edin v HESABLA dymsin sxn.</p>
            <form onSubmit={handleSubmit(onSubmit)}>
              <Controller
                name="country"
                control={control}
                rules={{ required: true }}
                render={({ field }) => <CustomSelect options={options} label="lk:" {...field} />}
              />
              {errors.country && <p className="errorMsg">lkni sein..</p>}
              <div className="volume_components">
                <label>
                  <span>Uzunluq (sm):</span>
                  <input
                    type="text"
                    className="text_input"
                    {...register('length', {
                      pattern: {
                        value: /^[0-9]+$/,
                        message: 'Please enter a number',
                      },
                    })}
                  />
                  {errors.length && <p className="errorMsg">Mhsulun uzunluunu dzgn daxil edin..</p>}
                </label>
                <label className="central">
                  <span>Eni (sm):</span>
                  <input
                    type="text"
                    className="text_input"
                    {...register('width', {
                      pattern: {
                        value: /^[0-9]+$/,
                        message: 'Please enter a number',
                      },
                    })}
                  />
                  {errors.width && <p className="errorMsg">Mhsulun enini dzgn daxil edin..</p>}
                </label>
                <label>
                  <span>Hndrlk (sm):</span>
                  <input
                    type="text"
                    className="text_input"
                    {...register('height', {
                      pattern: {
                        value: /^[0-9]+$/,
                        message: 'Please enter a number',
                      },
                    })}
                  />
                  {errors.height && <p className="errorMsg">Mhsulun hndrlyn dzgn daxil edin..</p>}
                </label>
              </div>
              <div className="weight">
                <div className="unit">
                  <Controller
                    name="unitWeight"
                    control={control}
                    rules={{ required: true }}
                    render={({ field }) => <CustomSelect options={units} label="ki vahidi " {...field} />}
                  />
                  {errors.unitWeight && <p className="errorMsg">ki vahidini sein..</p>}
                </div>
                <div className="weight_container">
                  <label>
                    <span>ki:</span>
                    <input
                      type="text"
                      className="text_input"
                      {...register('weight', {
                        required: 'bla',
                        pattern: {
                          value: /^[0-9]+$/,
                          message: 'Please enter a number',
                        },
                      })}
                    />
                    {errors.weight && <p className="errorMsg">kini daxil edin..</p>}
                  </label>
                </div>
              </div>
              <div className="calculation">
                <ValuePrinter resultValue={calculatorState.result} />
                <CalculatorButton />
              </div>
            </form>
          </div>
          <img src={require('./images/calculator.png')} alt="airplane" />
        </div>
      </div>
    </section>
  );
};
function setCalculationResult(): any {
  throw new Error('Function not implemented.');
}
